<template>
  <div style="position:relative;-margin:20px 0;margin-top:10px;font-size:13px;">
    <van-tabs v-model='actived'>
      <van-tab title="基础房型">
        <my-scale :myActived='actived'></my-scale>
      </van-tab>
      <van-tab title="产品房型">
        <my-pro :myActived='actived'></my-pro>
      </van-tab>
      <van-tab title="房间管理">
        <my-room :myActived='actived'></my-room>
      </van-tab>
    </van-tabs>

  </div>
</template>
<script>
import MyScale from './myScale';
import MyPro from './myPro';
import MyRoom from './myRoom';
export default {
    components: {
        MyScale,
        MyPro,
        MyRoom
    },
    data() {
        return {
            actived: 0
        };
    },
    created() {
        this.actived = Number(localStorage.getItem('myscale_Actived')) || 0;
    }
};
</script>

<style scoped>
/deep/ .van-tab--active {
    color: #ff7d41;
    font-weight: 500;
}
/deep/ .van-tabs__line {
    background-color: #ff7d41;
    z-index: 0;
}
</style>



