<template>
  <!-- 产品房型 -->
  <div id="scroll-pro">
    <div style="-margin-bottom:30px;">
      <div
        class="info"
        v-for="(pro,i) in proList"
        :key="i"
      >
        <div class="info-top">
          <div class="top-left">
            <div class="left-top">
              <div class="pr8 pro-overflow">{{ pro.scaleName }}</div>
              <div
                class="font12"
                :class="'c_'+pro.scaleType"
              >{{ orderTypeObj[pro.scaleType] }}</div>
            </div>
            <div class="left-bottom c_b2 font12">
              <div class="pr8">{{ JSON.parse(pro.rule).cancelRule.cancel == '1'?JSON.parse(pro.rule).cancelRule.hour+'之前能取消':'不能取消' }}</div>
              <div>{{ pro.isBreakfast == '1'?'含早':'不含早' }}</div>
            </div>
          </div>
          <div
            class="top-right"
            @tap.stop.prevent
          >
            <div class="c_b2 pr8 font12">{{ pro.isLockstatus == false?'已关房':'已开房' }}</div>
            <van-switch
              size="20px"
              active-color='#ff7d41'
              @change="onChange(pro)"
              v-model="pro.isLockstatus"
            />
          </div>
        </div>
        <div class='info-bottom'>
          <div class="font12">&yen;&ensp;{{ pro.realAmount }}/{{ orderTypeUnitObj[pro.scaleType] }}</div>
          <div class="bottom-edit">
            <span
              @click="goNext('/addOrEditPro', pro)"
              style="margin-left:24px;"
            >编辑</span>
            <van-icon
              class="c_b2"
              name="arrow"
            />
          </div>
        </div>
      </div>
      <van-button
        class="add-scale"
        @click="goNext('/addOrEditPro')"
      >+&ensp;添加产品</van-button>
    </div>
  </div>
</template>
<script>
import { PostBusinessVirtualRoomscaleOn, PostBusinessVirtualRoomscaleOff, GetBusinessVirtualRoomscaleList, PostBusinessVirtualRoomscaleRemove } from '@/api/api_business_hotel_and_scale';
import { GetSystemDictDataByType } from '@/api/api_query';
import { Toast, Dialog } from 'vant';
export default {
    props: ['myActived'],
    data() {
        return {
            checked: false,
            proList: [],
            orderTypeObj: {},
            orderTypeUnitObj: {
                1: '晚',
                2: '晚',
                3: '小时',
                4: '月',
                5: '天'
            },
            proListNum: 0
        };
    },
    watch: {
        myActived(neww) {
            if (neww == 1) {
                if (this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName'))) {
                    this.getScaleInfo(this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName')).roomScaleId);
                }
            }
        }
    },
    created() {
        if (this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName'))) {
            this.getScaleInfo(this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName')).roomScaleId);
        }
    },
    methods: {
        // 基础房型信息
        getScaleInfo(id) {
            this.proListNum = 0;
            GetBusinessVirtualRoomscaleList({ roomScaleId: id }).done((res) => {
                res.data.map((item) => {
                    item.isLockstatus = item.isLock == '0' ? true : false;
                    return item;
                });
                res.data.map((item) => {
                    if (item.isLock == 0) {
                        this.proListNum++;
                    }
                });
                this.proList = res.data;
                this.getType();
            });
        },
        // 查询字典
        getType() {
            GetSystemDictDataByType({ dictType: 'business_virtual_room_scale_type' }).done((res) => {
                this.orderTypeObj = {};
                res.data.map((item) => {
                    this.orderTypeObj[item.dictValue] = item.dictLabel;
                });
            });
        },
        // 关房-开房
        onChange(isLockInfo) {
            this.currentUserInfo = JSON.parse(localStorage.getItem('current_user'));
            if (this.currentUserInfo.id == '1' || this.currentUserInfo.permissions.find((item) => item == 'business:roomscale:remove')) {
                if (isLockInfo.isLockstatus == true) {
                    PostBusinessVirtualRoomscaleOn(isLockInfo.id).done((res) => {
                        Toast(res.msg);
                        this.proListNum++;
                    });
                } else {
                    if (this.proListNum == 1) {
                        Toast('至少开启一个产品房型');
                        isLockInfo.isLockstatus = true;
                        return false;
                    }
                    PostBusinessVirtualRoomscaleOff(isLockInfo.id).done((res) => {
                        Toast(res.msg);
                        this.proListNum--;
                    });
                }
            } else {
                Toast('权限不足');
                isLockInfo.isLockstatus = !isLockInfo.isLockstatus;
            }
        },
        // 删除产品房型
        removeProScale(item) {
            Dialog.confirm({
                title: '确定删除？',
                message: `产品房型：${item.scaleName}`
            })
                .then(() => {
                    PostBusinessVirtualRoomscaleRemove({ virtualRoomScaleId: item.id }).done((res) => {
                        this.getScaleInfo(this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName')).roomScaleId);
                    });
                })
                .catch(() => {});
        },
        // 添加产品房型
        goNext(url, pro) {
            let scaleInfo = JSON.parse(localStorage.getItem('addoredit_scaleName'));
            if (scaleInfo && scaleInfo.roomScaleId) {
                this.$router.push({
                    path: url,
                    query: {
                        hotelId: this.$route.query.hotelId,
                        myActived: this.myActived,
                        virtualRoomScaleId: pro ? pro.id : ''
                    }
                });
            } else {
                Toast('暂无基础房型');
            }
        }
    }
};
</script>
<style scoped>
#scroll-pro {
    /* border: 1px solid pink; */
    position: fixed;
    top: 115px;
    bottom: 10px;
    left: 0;
    right: 0;
    overflow-y: scroll;
    color: #000;
}
.info {
    /* border: 1px solid green; */
    padding: 10px 16px;
    line-height: 20px;
    background: #fff;
    color: #000;
    border-bottom: 1px solid #f2f3f5;
}
.info-top {
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.top-left {
    /* border: 1px solid pink; */
    width: 70%;
}
.left-top {
    /* border: 1px solid red; */
    display: flex;
}
.left-bottom {
    /* border: 1px solid red; */
    display: flex;
}
.top-right {
    /* border: 1px solid green; */
    display: flex;
    align-items: flex-end;
}
.info-bottom {
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-between;
}
.bottom-edit {
    display: flex;
    align-items: center;
}
.add-scale {
    color: #1989fa;
    line-height: 40px;
    width: 100%;
}
.pro-overflow {
    color: #000;
    max-width: 60%;
    text-align: left;
    font-weight: bold;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 15px;
}
.c_b2 {
    color: #b2b2b2;
}
.c_1,
.c_2 {
    color: #ff7d41;
    border: 1px solid #ffe4d7;
    border-radius: 2px;
}
.c_3 {
    color: #048fe5;
    border: 1px solid #b5dbf3;
    border-radius: 2px;
}
.c_4,
.c_5 {
    color: #02bc6c;
    border: 1px solid #abf9d7;
    border-radius: 2px;
}
.c_FF5257 {
    color: #ff5257;
}
.pr8 {
    padding-right: 8px;
}
.font12 {
    font-size: 12px;
    padding: 0 2px;
    height: 18px;
}
</style>



