<template>
  <div style="position:relative;">
    <van-form
      @submit="onSubmit"
      id="scroll-scale"
    >
      <div class="title">基础信息</div>
      <van-field
        v-model="dataForm.scaleName"
        :readonly='readonly'
        label="房型名称"
        maxlength='64'
        placeholder="请输入房型名称"
        required
        :rules="[{ required: true, message: '请填写请输入房型名称' }]"
      />
      <van-field
        v-model="dataForm.roomSize"
        :readonly='readonly'
        label="房间大小(㎡)"
        maxlength='10'
        placeholder="请输入房间大小"
      />
      <van-field
        v-if="readonly == true"
        v-model="dataForm.personNum"
        readonly
        label="可住人数"
      />
      <div
        class="roomNum"
        v-else
      >
        <div style="width:90px;">可住人数</div>
        <van-stepper v-model='dataForm.personNum' />
      </div>
      <van-field
        v-if="readonly == true"
        v-model="dataForm.roomNum"
        readonly
        label="套内房间数"
      />
      <div
        class="roomNum"
        v-else
      >
        <div style="width:90px;">套内房间数</div>
        <van-stepper v-model='dataForm.roomNum' />
      </div>
      <van-field
        v-model="dataForm.bedSize"
        :readonly='readonly'
        label="床宽(m)"
        maxlength='32'
        placeholder="请输入床宽"
      />
      <van-field
        v-if="readonly == true"
        v-model="dataForm.bedNum"
        readonly
        label="床数量(张)"
      />
      <div
        class="roomNum"
        v-else
      >
        <div style="width:90px;">床数量(张)</div>
        <van-stepper v-model='dataForm.bedNum' />
      </div>
      <van-field
        v-model="windowType"
        :readonly='readonly == false?false:true'
        label="是否有窗"
        @click.native="show_window = readonly == false?true:false"
      />
      <div
        v-if="readonly == true"
        class="roomNum"
        style="justify-content:normal;"
      >
        <div style="width:140px;margin-right:12px;">空调</div>
        <div style="color:#000;">
          {{ airConditionerChecked == false?'无空调':'有空调' }}
        </div>
      </div>
      <div
        v-else
        class="roomNum"
      >
        <div style="width:90px;">空调</div>
        <div
          style="display:flex;align-items:center;"
          @click="airChange"
        >
          <img
            v-if="airConditionerChecked == true"
            class="abled-back"
            src="@/assets/btn_selected.png"
            alt
          />
          <div
            v-else
            style="width:18px;height:18px;border-radius:16px;border:1px solid #c8c9cc;"
          >
          </div>
        </div>
      </div>
      <van-field
        style="margin: 12px 0;"
        v-model="bookMonth"
        :readonly='readonly == false?false:true'
        label="提前预定"
        @click.native="show_bookMonth = readonly == false?true:false"
      />
      <div
        class="title"
        style="margin-top: 12px;"
      >日租保洁外包费用单价<span style='margin-left:8px;font-size:12px;color:#cecece;'>非必填</span></div>
      <div style="display:flex;background:#fff;align-items:baseline;">
        <van-field
          v-model="dataForm.cleanAmount"
          :readonly='readonly'
          type="number"
          style="width:80%;"
          label="退房打扫"
          placeholder="请输入退房打扫费用"
        />
        <span v-if="readonly == false || dataForm.cleanAmount">/次</span>
      </div>
      <div style="display:flex;background:#fff;align-items:baseline;">
        <van-field
          v-model="dataForm.cleaningAmount"
          :readonly='readonly'
          style="width:80%;"
          type="number"
          label="续住打扫"
          placeholder="请输入续住打扫费用"
        />
        <span v-if="readonly == false || dataForm.cleaningAmount">/次</span>
      </div>
      <!-- 长短租保洁外包费用单价 -->
      <div
        class="title"
        style="margin-top: 12px;"
      >长短租保洁外包费用单价<span style='margin-left:8px;font-size:12px;color:#cecece;'>非必填</span></div>
      <div style="display:flex;background:#fff;align-items:baseline;">
        <van-field
          v-model="dataForm.monthCleanAmount"
          :readonly='readonly'
          type="number"
          style="width:80%;"
          label="退房打扫"
          placeholder="请输入退房打扫费用"
        />
        <span v-if="readonly == false || dataForm.monthCleanAmount">/次</span>
      </div>
      <div
        style="display:flex;background:#fff;align-items:baseline;"
        :style="{'margin-bottom':dataForm.id?'':'60px'}"
      >
        <van-field
          v-model="dataForm.monthCleaningAmount"
          :readonly='readonly'
          style="width:80%;"
          type="number"
          label="续住打扫"
          placeholder="请输入续住打扫费用"
        />
        <span v-if="readonly == false || dataForm.monthCleaningAmount">/次</span>
      </div>
      <!-- 默认保洁项目 -->
      <div
        v-if="dataForm.id"
        class="normal_flex"
        @click="goNext('/normal-project')"
      >
        <div>默认保洁项目</div>
        <div class="c_98">
          <van-icon
            name="arrow"
            color="#b2b2b2"
          />
        </div>
      </div>
      <!-- 按钮 -->
      <div class="bottom-button">
        <van-button
          v-if="localStorageScaleInfo"
          class="submit-btn submit-normal"
          block
          @click="removeScale('remove')"
          native-type="submit"
        >
          删除基础房型
        </van-button>
        <van-button
          class="submit-btn"
          block
          @click="addOrEditScale('addOrEdit')"
          native-type="submit"
        >
          {{ myScaleTitle }}
        </van-button>
      </div>
    </van-form>

    <!-- 是否窗户 -->
    <van-popup
      v-model="show_window"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="windowTypeAll"
        @cancel="show_window = false"
        @confirm="confirmWindow"
      />
    </van-popup>
    <!-- 预定期限 -->
    <van-popup
      v-model="show_bookMonth"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="bookMonthList"
        @cancel="show_bookMonth = false"
        @confirm="confirmBookMonth"
      />
    </van-popup>
  </div>
</template>
<script>
import { Toast } from 'vant';
import { PostBusinesSroomscaleAdd, PostBusinesSroomscaleEdit, PostBusinesSroomscaleRemove } from '@/api/api_hotel.js';
import { GetBusinessRoomscale } from '@/api/api_business_hotel_and_scale';
import { GetSystemDictDataByType } from '@/api/api_query';
import { Dialog } from 'vant';
export default {
    props: ['myActived'],
    data() {
        return {
            stepper: 1,
            myScaleTitle: '修改房型',
            showPicker: false,
            show_window: false,
            show_bookMonth: false,
            readonly: true,
            bookMonthList: ['1', '2', '3', '4'],
            airConditionerChecked: false,
            dataForm: {
                id: this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName')) ? JSON.parse(localStorage.getItem('addoredit_scaleName')).roomScaleId : '',
                hotelId: this.$route.query.hotelId,
                scaleName: null,
                roomSize: null,
                bedSize: null,
                roomNum: null,
                bedNum: null,
                windowType: null,
                airConditioner: null,
                bookMonth: null,
                cleanAmount: null,
                cleaningAmount: null,
                monthCleanAmount: null,
                monthCleaningAmount: null,
                personNum: null
            },
            columns: [],
            columnsAll: [],
            windowType: null,
            bookMonth: null,
            windowTypeAll: [],
            orderTypeObj: {}
        };
    },
    watch: {
        myActived(neww) {
            if (neww == 0) {
                this.getType();
            }
        }
    },
    created() {
        this.currentUserInfo = JSON.parse(localStorage.getItem('current_user'));
        this.getType();
    },
    methods: {
        // 查询字典
        getType() {
            this.localStorageScaleInfo = JSON.parse(localStorage.getItem('addoredit_scaleName'));
            GetSystemDictDataByType({ dictType: 'business_window_type' }).done((res) => {
                this.windowTypeOldAll = res.data;
                this.windowTypeAll = res.data.map((item) => item.dictLabel);
                if (this.localStorageScaleInfo) {
                    this.getScaleInfo(this.localStorageScaleInfo.roomScaleId);
                } else {
                    this.windowType = this.windowTypeOldAll[3].dictLabel + ' >';
                    this.dataForm.windowType = this.windowTypeOldAll[3].dictValue;
                    this.bookMonth = this.bookMonthList[2] + '个月' + ' >';
                    this.dataForm.bookMonth = this.bookMonthList[2];
                    this.myScaleTitle = '保存修改';
                    this.readonly = false;
                }
            });
        },
        // 基础房型信息
        getScaleInfo(id) {
            GetBusinessRoomscale(id).done((res) => {
                this.dataForm = { ...this.dataForm, ...res.data };
                this.dataForm.roomSize = Number(this.dataForm.roomSize);
                this.airConditionerChecked = this.dataForm.airConditioner == 0 ? false : true;
                this.windowType = this.windowTypeOldAll.find((item) => item.dictValue == this.dataForm.windowType).dictLabel;
                this.bookMonth = this.dataForm.bookMonth + '个月';
                let obj = {
                    scaleName: this.dataForm.scaleName,
                    roomScaleId: this.dataForm.id
                };
                localStorage.setItem('addoredit_scaleName', JSON.stringify(obj));
                if (this.dataForm.otherAmount) {
                    let totlePrice = JSON.parse(this.dataForm.otherAmount);
                    for (const k in totlePrice) {
                        this.dataForm[k] = totlePrice[k];
                    }
                }
            });
        },
        // 空调
        airChange() {
            if (this.readonly == false) {
                this.airConditionerChecked = !this.airConditionerChecked;
            }
        },
        // 选择是否有窗
        confirmWindow(event) {
            this.windowType = event + ' >';
            this.dataForm.windowType = this.windowTypeOldAll.find((item) => item.dictLabel == event).dictValue;
            this.show_window = false;
        },
        // 预定期限
        confirmBookMonth(event) {
            this.bookMonth = event + '个月' + ' >';
            this.dataForm.bookMonth = event;
            this.show_bookMonth = false;
        },
        // 设置默认保洁项目
        goNext(url) {
            if (this.readonly == false) {
                this.$router.push({
                    path: url,
                    query: {
                        hotelId: this.dataForm.hotelId,
                        roomScaleId: this.dataForm.id
                    }
                });
            }
        },
        // 删除基础房型
        removeScale(type) {
            this.type = type;
        },
        // 新增或编辑
        addOrEditScale(type) {
            this.type = type;
        },
        //提交表单
        onSubmit() {
            if (this.type == 'remove') {
                if (this.currentUserInfo.permissions.find((item) => item == 'business:roomscale:remove')) {
                    Dialog.confirm({
                        title: '确定删除？',
                        message: `基础房型：${this.dataForm.scaleName}`
                    })
                        .then(() => {
                            PostBusinesSroomscaleRemove({ roomScaleId: this.dataForm.id }).done((res) => {
                                Toast(res.msg);
                                setTimeout('window.history.go(-1)', 900);
                            });
                        })
                        .catch(() => {});
                } else {
                    Toast('权限不足');
                }
            } else {
                this.readonly = false;
                if (this.myScaleTitle == '修改房型') {
                    this.myScaleTitle = '保存修改';
                    this.bookMonth = this.dataForm.bookMonth + '个月' + ' >';
                    this.windowType = this.windowTypeOldAll.find((item) => item.dictValue == this.dataForm.windowType).dictLabel + ' >';
                    return;
                }
                this.dataForm.airConditioner = this.airConditionerChecked == false ? '0' : '1';
                delete this.dataForm.rooms;
                let newFormData = {};
                newFormData.id = this.dataForm.id;
                newFormData.hotelId = this.dataForm.hotelId;
                newFormData.scaleName = this.dataForm.scaleName;
                newFormData.roomSize = this.dataForm.roomSize;
                newFormData.bedSize = this.dataForm.bedSize;
                newFormData.roomNum = this.dataForm.roomNum;
                newFormData.bedNum = this.dataForm.bedNum;
                newFormData.windowType = this.dataForm.windowType;
                newFormData.airConditioner = this.dataForm.airConditioner;
                newFormData.bookMonth = this.dataForm.bookMonth;
                newFormData.cleanAmount = this.dataForm.cleanAmount;
                newFormData.cleaningAmount = this.dataForm.cleaningAmount;
                newFormData.personNum = this.dataForm.personNum;
                newFormData.monthCleanAmount = this.dataForm.monthCleanAmount;
                newFormData.monthCleaningAmount = this.dataForm.monthCleaningAmount;
                if (this.dataForm.id) {
                    delete this.dataForm.virtualRoomScales;
                    PostBusinesSroomscaleEdit(newFormData).done((res) => {
                        Toast.success(res.msg);
                    });
                    let obj = {
                        scaleName: this.dataForm.scaleName,
                        roomScaleId: this.dataForm.id
                    };
                    localStorage.setItem('addoredit_scaleName', JSON.stringify(obj));
                    this.myScaleTitle = '修改房型';
                    this.bookMonth = this.dataForm.bookMonth + '个月';
                    this.windowType = this.windowTypeOldAll.find((item) => item.dictValue == this.dataForm.windowType).dictLabel;
                    this.readonly = true;
                } else {
                    PostBusinesSroomscaleAdd(newFormData).done((res) => {
                        Toast.success(res.msg);
                        let obj = {
                            scaleName: this.dataForm.scaleName,
                            roomScaleId: res.data.id
                        };
                        localStorage.setItem('addoredit_scaleName', JSON.stringify(obj));
                        this.windowType = this.windowTypeOldAll.find((item) => item.dictValue == this.dataForm.windowType).dictLabel;
                        this.bookMonth = this.dataForm.bookMonth + '个月';
                        this.myScaleTitle = '修改房型';
                        this.dataForm.id = res.data.id;
                        this.getType();
                        this.readonly = true;
                    });
                }
            }
        }
    }
};
</script>

<style scoped>
#scroll-scale {
    position: fixed;
    top: 115px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
}
.title {
    font-size: 14px;
    text-align: left;
    padding: 0 16px;
    line-height: 35px;
    background: #fff;
    border-bottom: 1px solid #f7f8fa;
    color: #000;
}
.submit-btn {
    background: #ff7d41;
    color: #fff;
}
.submit-normal {
    background: #989898;
}
.roomNum {
    display: flex;
    text-align: left;
    justify-content: space-between;
    padding: 10px 16px;
    align-items: center;
    background: #fff;
    border-bottom: 1px solid #f8f8f8;
}
.bottom-button {
    display: flex;
    border-radius: 8px;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}
.abled-back {
    width: 20px;
    height: 20px;
}
.normal_flex {
    margin-top: 12px;
    margin-bottom: 60px;
    background: #fff;
    display: flex;
    color: #000;
    justify-content: space-between;
    padding: 10px 18px;
}
.c_98 {
    color: #989898;
    display: flex;
    align-items: center;
}
/deep/ .van-field__control:disabled {
    color: #000;
}
/deep/ .van-field__label,
/deep/ .van-switch-cell {
    text-align: left;
    width: 140px;
}
/deep/ .van-cell {
    font-size: 13px;
}
</style>



