<template>
  <!-- 产品房型 -->
  <div id="scroll-pro">
    <div>
      <div
        class="info"
        v-for="(item,i) in myRoomsAll"
        :key="i"
      >
        <div class="info-top">
          <div class="top-left">
            <div style="display:flex;align-items:center;">
              <div style="font-size:18px;margin-right:10px;">{{ item.roomSn }}</div>
              <img
                v-if="!item.mac"
                style="width:16px;height:16px;"
                src="@/assets/room_model/close.png"
              >
              <img
                v-else
                style="width:16px;height:16px;"
                src="@/assets/room_model/open.png"
              >
            </div>
            <div class="font12">
              <span v-if="item.buildNum">{{ item.buildNum }}栋</span>
              <span v-if="item.unit">{{ item.unit }}单元</span>
              <span v-if="item.floor">{{ item.floor }}楼</span>
            </div>
          </div>
        </div>
        <div class='info-bottom'>
          <span
            v-if="item.status < '2' || item.status > '3'"
            class="c_FF5257"
            @click="cancelConnect(item)"
          >取消绑定</span>
        </div>
      </div>
      <van-button
        class="add-scale"
        @click="addRoom"
      >+&ensp;绑定房间</van-button>
      <!-- 新增房间 -->
      <van-popup
        v-model='show_add_room'
        :close-on-click-overlay='false'
        style="width:90%;border-radius:10px;"
        :click="confirmAddRoom"
      >
        <div>
          <div style="padding:20px 0;font-size:15px;">{{ dataForm.id?'编辑房间':'新增房间' }}</div>
          <van-field
            v-if="!dataForm.id"
            label="基础房型"
            v-model="myscaleInfo.scaleName"
            readonly
          />
          <van-field
            v-else
            label="基础房型"
            v-model="scaleName"
            readonly
            @click="show_scale = true"
          />
          <van-field
            label="房间编号"
            placeholder="请输入房间编号"
            v-model="dataForm.roomSn"
          />
          <van-field
            label="楼层号"
            type="number"
            placeholder="请输入楼层号(选填)"
            v-model="dataForm.floor"
          />
          <van-field
            label="楼栋号"
            placeholder="请输入楼栋号(选填)"
            v-model="dataForm.buildNum"
          />
          <van-field
            label="单元号"
            placeholder="请输入单元号(选填)"
            v-model="dataForm.unit"
          />
          <van-field
            label="分房排序"
            type="number"
            placeholder="请输入排序序号(选填)"
            v-model="dataForm.roomSort"
          />
          <van-field
            label="房间等级"
            v-model="roomLevel"
            readonly
            @click="show_level = true"
          />
          <van-field
            v-if="hotelInfo && hotelInfo.hotelType == '4'"
            label="床位数"
            type="number"
            placeholder="请输入床位数(选填)"
            v-model="dataForm.bedNum"
          />
          <div class="room_bottom">
            <div
              class="room_cancle"
              @click="show_add_room = false"
            >取消</div>
            <div
              class="room_ok"
              @click="confirmAddRoom"
            >确定</div>
          </div>
        </div>
      </van-popup>
      <!-- 选择基础房型 -->
      <van-popup
        v-model="show_scale"
        position="bottom"
      >
        <van-picker
          show-toolbar
          :columns="scaleAll"
          @confirm="onConfirmScale"
          @cancel="show_scale = false"
        />
      </van-popup>
      <!-- 选择房间等级 -->
      <van-popup
        v-model="show_level"
        position="bottom"
      >
        <van-picker
          show-toolbar
          :columns="levelAll"
          @confirm="onConfirmLevel"
          @cancel="show_level = false"
        />
      </van-popup>
      <!-- 选择房间等级 -->
      <van-popup
        v-model="show_addRoom"
        position="bottom"
        style="font-weight:bold;font-size:14px;"
      >
        <div class="room_left">
          <div></div>
          <div>请选择房间号</div>
          <div
            style="color:#999;font-weight:normal;"
            @click="show_addRoom = false;"
          >取消</div>
        </div>
        <div style="height:5px;background:#f5f2f6;"></div>
        <div
          v-for="(item,i) in chooseRoomAll"
          :key="i"
          class="room_content"
          @click="pointRoom(item)"
        >
          <van-icon
            :color="item.my_statu == true?'#ff7d41':''"
            style="margin:0 40px;"
            :name="item.my_statu == true?'checked':'circle'"
          />
          <div>{{ item.roomSn }}</div>
        </div>
        <div style="height:5px;background:#f5f2f6;"></div>
        <div
          style="padding:12px;"
          @click="sub_room"
        >确定</div>
      </van-popup>
      <!-- 扩容 -->
      <van-dialog
        v-model="setMealShow"
        title='房间数量已达上限'
        confirm-button-text="我知道了"
        cancel-button-text='扩容'
        show-cancel-button
        @confirm='setMealShow = false'
        @cancel='confirm'
      >
        <div
          v-if="hotelInfo"
          class="centent"
        >当前可使用房间数量为{{ hotelInfo.roomCap }}间,请点击扩容后新增房间。</div>
      </van-dialog>
    </div>
  </div>
</template>
<script>
import { GetBusinessRoomNumByhotel } from '@/api/api_soft';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessHotel, GetBusinessRoomByRoomScale, PostBusinessRoomAdd, PostBusinessRoomEdit, PostBusinessRoomRemove } from '@/api/api_hotel.js';
import { GetBusinessRoom, PostBusinessRoomBondByRoomScale } from '@/api/api_business_hotel_and_scale';
import { Toast, Dialog } from 'vant';
export default {
    props: ['myActived'],
    data() {
        return {
            show_add_room: false,
            show_scale: false,
            show_level: false,
            setMealShow: false,
            show_addRoom: false,
            myscaleInfo: {},
            myRoomsAll: [],
            scaleAll: [],
            levelAll: [],
            chooseRoomAll: [],
            dataForm: {
                hotelId: this.$route.query.hotelId,
                roomScaleId: this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName')) ? JSON.parse(localStorage.getItem('addoredit_scaleName')).roomScaleId : '',
                id: null,
                roomSn: null,
                floor: null,
                buildNum: null,
                unit: null,
                roomLevel: null,
                roomSort: '0',
                bedNum: null
            },
            roomLevel: null,
            realRooms: null,
            scaleName: null,
            hotelInfo: null
        };
    },
    watch: {
        myActived(neww) {
            if (neww == 2) {
                if (this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName'))) {
                    this.getScaleInfo();
                }
            }
        }
    },
    created() {
        this.getHotelInfo();
    },
    methods: {
        // 酒店信息
        getHotelInfo() {
            GetBusinessHotel({ hotelId: this.$route.query.hotelId }).then((res) => {
                this.hotelInfo = res.data;
                if (this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName'))) {
                    this.myscaleInfo = JSON.parse(localStorage.getItem('addoredit_scaleName'));
                    this.getScaleInfo();
                }
            });
        },
        // 根据条件查询房间
        getScaleInfo() {
            let roomScaleId = this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName')) ? JSON.parse(localStorage.getItem('addoredit_scaleName')).roomScaleId : '';
            GetBusinessRoomByRoomScale({ roomScaleId: roomScaleId }).done((res) => {
                this.myRoomsAll = res.data;
                this.getRooms();
            });
        },
        // 查询酒店房间数量
        getRooms() {
            GetBusinessRoomNumByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.realRooms = res.data;
            });
        },
        // 取消绑定
        cancelConnect(item) {
            Dialog.confirm({
                title: '取消绑定？',
                message: `房间号：${item.roomSn}`
            })
                .then(() => {
                    let data = {
                        id: item.id,
                        roomScaleId: '0'
                    };
                    PostBusinessRoomEdit(data).done((res) => {
                        Toast(res.msg);
                        this.getScaleInfo();
                    });
                })
                .catch(() => {});
        },
        // 删除房间
        removeRoom(item) {
            Dialog.confirm({
                title: '确定删除？',
                message: `房间号：${item.roomSn}`
            })
                .then(() => {
                    PostBusinessRoomRemove({ roomId: item.id }).done((res) => {
                        Toast(res.msg);
                        this.getScaleInfo();
                    });
                })
                .catch(() => {});
        },
        // 绑定房间
        addRoom() {
            let scaleInfo = JSON.parse(localStorage.getItem('addoredit_scaleName'));
            if (scaleInfo && scaleInfo.roomScaleId) {
                let data = {
                    hotelId: this.$route.query.hotelId,
                    roomScaleId: '0',
                    isGate: '0'
                };
                GetBusinessRoom(data).done((res) => {
                    this.chooseRoomAll = res.data.map((item) => {
                        item.my_statu = false;
                        return item;
                    });
                    this.show_addRoom = true;
                });
            } else {
                Toast('暂无基础房型');
            }
        },
        // 选中房间
        pointRoom(item) {
            item.my_statu = !item.my_statu;
        },
        // 选择房间
        sub_room() {
            let choosedRoom = this.chooseRoomAll.filter((item) => item.my_statu == true);
            if (choosedRoom.length == '0') {
                Toast('请选择房间号');
                return;
            } else {
                let data = {
                    roomScaleId: this.$route.query.id,
                    roomIds: choosedRoom.map((item) => item.id).toString()
                };
                PostBusinessRoomBondByRoomScale(data).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                    this.getHotelInfo();
                });
            }
            this.show_addRoom = false;
        },
        // 详情
        goNext(url, item) {
            this.$router.push({
                path: url,
                query: {
                    hotelId: this.$route.query.hotelId,
                    id: item.id
                }
            });
            localStorage.setItem('myscale_Actived', 2);
        },
        // 字典查询
        getType() {
            GetSystemDictDataByType({ dictType: 'business_room_level' }).done((res) => {
                this.levelOldAll = res.data;
                this.levelAll = res.data.map((item) => item.dictLabel);
                if (this.dataForm.id) {
                    this.show_add_room = true;
                    this.roomLevel = this.levelOldAll.find((item) => item.dictValue == this.dataForm.roomLevel).dictLabel + ' >';
                } else {
                    this.dataForm.roomSort = '0';
                    this.roomLevel = this.levelOldAll[0].dictLabel + ' >';
                    this.dataForm.roomLevel = this.levelOldAll[0].dictValue;
                    if (this.hotelInfo.roomCap == this.realRooms) {
                        this.setMealShow = true;
                    } else {
                        this.show_add_room = true;
                    }
                }
            });
        },
        // 扩容
        confirm() {
            this.$router.push({
                path: '/continuePayChoose',
                query: {
                    hotelId: this.$route.query.hotelId,
                    roomCap: this.hotelInfo.roomCap,
                    endLineTime: this.$route.query.endLineTime,
                    round: this.$route.query.round,
                    charge: this.$route.query.charge
                }
            });
            this.setMealShow = false;
        },
        // 选择房间等级
        onConfirmLevel(e) {
            this.roomLevel = e + ' >';
            this.dataForm.roomLevel = this.levelOldAll.find((item) => item.dictLabel == e).dictValue;
            this.show_level = false;
        },
        // 选择基础房型
        onConfirmScale(e) {
            this.scaleName = e + ' >';
            this.dataForm.roomScaleId = this.scaleOldAll.find((item) => item.scaleName == e).id;
            this.show_scale = false;
        },
        // 提交表单
        confirmAddRoom() {
            if (!this.dataForm.roomSn) {
                Toast('请输入房间编号');
                return;
            }
            if (!this.dataForm.id) {
                this.dataForm.roomScaleId = this.$route.query.id || JSON.parse(localStorage.getItem('addoredit_scaleName')) ? JSON.parse(localStorage.getItem('addoredit_scaleName')).roomScaleId : '';
            }
            if (this.hotelInfo.hotelType != '4') {
                delete this.dataForm.bedNum;
            }
            if (this.dataForm.id) {
                PostBusinessRoomEdit(this.dataForm).done((res) => {
                    Toast(res.msg);
                    this.getScaleInfo();
                });
            } else {
                this.dataForm.isGate = 0;
                this.dataForm.roomName = this.dataForm.roomSn;
                PostBusinessRoomAdd(this.dataForm).done((res) => {
                    Toast(res.msg);
                    this.getScaleInfo();
                });
            }
            this.show_add_room = false;
        }
    }
};
</script>
<style scoped>
#scroll-pro {
    position: fixed;
    top: 115px;
    bottom: 10px;
    left: 0;
    right: 0;
    overflow-y: scroll;
    color: #000;
}
.info {
    padding: 10px 16px;
    line-height: 20px;
    background: #fff;
    color: #000;
    border-bottom: 1px solid #f2f3f5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    font-size: 14px;
    height: 30px;
}
.info-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.info-bottom {
    font-size: 13px;
    display: flex;
    align-items: center;
}
.add-scale {
    color: #1989fa;
    line-height: 40px;
    width: 100%;
}
.room-box {
    width: 70%;
    border-radius: 8px;
}
.c_FF5257 {
    color: #ff5257;
}
.c_b2 {
    color: #b2b2b2;
}
.p8 {
    margin-left: 30px;
}
.font12 {
    font-size: 12px;
    color: #989898;
}
.room_bottom {
    display: flex;
    justify-content: space-around;
    width: 10;
    width: 100%;
    line-height: 50px;
    font-size: 16px;
}
.room_cancle {
    color: #000;
    border: 1px solid #d3d3d3;
    width: 50%;
}
.room_ok {
    width: 50%;
    color: #ff7d41;
    border: 1px solid #d3d3d3;
}
.centent {
    width: 80%;
    margin: 20px auto;
    text-align: center;
    font-size: 14px;
    color: #888;
}
.room_left {
    display: flex;
    justify-content: space-between;
    padding: 12px 30px;
}
.room_content {
    display: flex;
    align-items: center;
    padding: 15px 0;
    border-bottom: 1px solid #f5f2f6;
    font-size: 13px;
}
/deep/ .van-dialog__header {
    font-weight: bold;
    font-size:18px;
}
/deep/ .van-dialog__footer {
    display: block;
}
/deep/ .van-dialog__cancel {
    background: #ff7d41;
    width: 80%;
    color: #fff;
    border-radius: 20px;
    height: 34px;
    margin: 12px 0;
}
/deep/ .van-dialog__confirm {
    background: #fff;
    border: 1px solid #e6e6e6;
    color: #000;
    width: 80%;
    border-radius: 20px;
    height: 34px;
    margin: 12px 0;
}
/deep/ .van-cell {
    line-height: 30px;
}
/deep/ .van-field__control {
    background: #efeff4;
    text-indent: 18px;
}
/deep/ .van-dialog {
    margin-top: 60px;
}
/deep/ .van-field__control:read-only {
    cursor: default;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
</style>



